@import 'https://www.contradance.org/core/modules/system/css/components/align.module.css';


html {
    font-size: 1.5em;
    font-family: "Helvetica Neue", Helvetica, Arial, sans-serif;
    overflow: visible !important;
}
.card {
    background-color: black;
}

body header a.nav-link {
    text-decoration: none;
}


.event-name {
    font-size:2em;
    line-height: 1.25em;
}

.event-container {
    line-height: 1.25em;
}

.event-name a {
    text-decoration: none;
}

body .main-container {
    -webkit-animation: fadein 3s; /* Safari, Chrome and Opera > 12.1 */
    -moz-animation: fadein 3s; /* Firefox < 16 */
    -o-animation: fadein 3s; /* Opera < 12.1 */
    animation: ease-in fadein 3s;
}

div.video-background {
    height: 100%;
}

video.vjs-tech {
    width: 100%;
}

div#video-background div, div#video-background button,
div#video-foreground div, div#video-foreground button  {
    display:none !important;
}

div#video-foreground {
    width: 100%;
    height: auto;
}

div#video-foreground {
    opacity: 1;
}

div#video-background {
    opacity: .3;
}
nav.navbar .navbar-collapse div.nav a {
    padding-left: 15px;
    padding-right: 15px;
}

footer {
    display: block !important;
}

footer div > a > img {
    height: 100px;
    width: auto;
    margin-bottom: 1em;
}

nav.navbar {
    margin-bottom: 0;
}

@media only screen and (max-width: 992px) {
    .collapse:not(.show) {
        display: none !important;
    }
}
